.message-screen {
  background: #000;
  width: 70.5vw;
  height: 80vh;
  margin-top: 50px;
  position: fixed;
  overflow-y: scroll;
}
.Type {
  transform: translate(0, 78vh);
  height: 63px;
  display: flex;
  align-items: center;
  margin-left: 0px;
  border-top: solid;
  border-color: var(--bg-primary-write);
  z-index: 9;
  background: #000;
  position: fixed;
  width: 71.5vw;
}
.message-input {
  width: 85%;
  height: 30px;
  background: #000;
  border: none;
  color: white;
  font-size: 20px;
}
.smilie {
  width: 30px;
  height: 30px;
  padding-right: 15px;
}
.send-container {
  background: var(--bg-primary-light);
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  padding: 5px;
  margin-left: 10px;
  transition: all 0.5s ease;
  cursor: pointer;
}
.send-container:hover {
  background: #000;
  color: var(--bg-primary-light);
  border: solid;
}
.send-icon {
  width: 25px;
  height: 25px;
}

.message-container {
  width: 85%;
  border-bottom: solid;
  border-color: var(--bg-light);
  height: 40px;
  padding: 3px;
  display: flex;
  margin-left: 40px;
}
.message-input::placeholder {
  color: var(--bg-light);
  font-size: 15px;
}
input:focus {
  outline: none;
}
.Messages {
  background: teal;
  margin-top: 10px;
  height: 0px;
}
