.Navcontent-container {
  background: rgb(255, 255, 255);
  z-index: 9;
  width: 80px;
  height: 100vh;
  display: flex;
  position: fixed;
}
.nav-icons {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border: solid;
}
.nav-icon {
  padding: 10px;
  width: 25px;
  height: 25px;
  transition: all 0.5s ease;
  color: var(--bg-primary-write);
}
.nav-icon:hover {
  width: 24px;
  height: 24px;
  background: var(--bg-primary-write);
  color: white;
  border-radius: 50%;
  transition: all 0.5s ease;
}
.navicon-top {
  display: flex;
  flex-direction: column;
  margin-top: -100px;
  margin-left: 50%;
}
.navicon-bottom {
  display: flex;
  flex-direction: column;
  margin-left: 50%;
  position: relative;
  bottom: -100px;
}
