@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto:wght@300&display=swap");

.People-container {
  color: white;
  background: var(--bg-primary);

  padding-left: 65px;
  margin-top: 50px;
  font-family: "Poppins", sans-serif;
}
.People {
  display: flex;
  height: 60px;
  border-top: solid;
  border-width: 1px;
  padding-top: 20px;
  padding-bottom: -20px;
  border-color: var(--bg-light);
  transition: all ease 0.5s;
  cursor: pointer;
}
.People:hover {
  color: var(--bg-primary-write);
  background-color: white;
}
.People-image {
  flex: 1;
}
.People-image > img {
  width: 40px;
  border-radius: 100%;
}
.People-details {
  flex: 5;
  align-items: center;
  margin-left: -10px;
  margin-top: 5px;
}
.top-row {
  margin-top: -10%;
  padding-left: 5%;
  display: flex;
  width: 80%;
  align-items: center;
  justify-content: space-between;
}
.last-message {
  text-align: left;
  margin-top: -25px;
  display: flex;
  gap: 0.5rem;
  padding-left: 10px;
  padding-top: 5px;
  width: 100%;
  font-size: 15px;
  font-family: "Poppins", sans-serif;
}
.tick {
  color: rgb(25, 202, 222);
  width: 20px;
  height: 20px;
}
.message-icon {
  display: inline;
}
.message-content {
  width: 100%;
  font-size: 13px;
  margin-left: -5px;
}
.Message-heading {
  font-size: 25px;
  padding-top: 10px;
  margin-bottom: 3px;
}
.People-time {
  font-size: 12px;
}
