@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto:wght@300&display=swap");
.navbar1-container {
  padding: 10px;
  display: flex;
  background: black;
  margin-left: 60px;
  height: 30px;
  position: fixed;
  width: 22.15%;
}
.navbar1-heading {
  font-size: 20px;
  flex: 1;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
}
.navbar1-icon {
  flex: 1;
  width: 40px;
  height: 40px;
  margin: auto;
}
.navbar1-search {
  flex: 0.5;
  margin: auto;
  width: 25px;
  height: 25px;
  color: var(--bg-light);
}
.search-bar {
  background: #000;
  border: none;
  font-size: 20px;
  color: white;
}
.search-bar:focus {
  border: none;
}
.line {
  background: var(--bg-light);
  height: 1px;
}
.navbar2-container {
  display: flex;
  background: var(--bg-primary-light);
  height: 50px;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  width: 78%;
}
.navbar2-image {
  flex: 0.3;
  margin: auto;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.image {
  background: teal;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  padding: 10px;
}
.person-name {
  flex: 1;
  font-size: 20px;
  margin-left: -13px;
  margin-top: -1px;
  font-family: "Poppins", sans-serif;
  height: 40px;
}
.name {
  font-size: 17px;
  margin-bottom: 1px;
  font-weight: 600;
}
.activity {
  font-size: 10px;
  margin-top: -6px;
  color: var(--bg-primary-two);
}
.symbols {
  flex: 1;
  display: flex;
  align-items: center;
  margin-left: 350px;
}
.symbol {
  padding: 10px;
  width: 22px;
  height: 22px;
  transition: all 0.5s ease;
}
.symbol:hover {
  background: #000;
  border-radius: 50%;
  padding: 15px;
}
